@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
div.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 90%;
    margin: 0 auto;
    background-color: rgb(253, 250, 235);
}

header {
    flex-basis: 40vh;
    border-bottom: 10px solid #000;
}

main {
    display: flex;
    flex-grow: 1;
    padding: 30px 0;
}

main aside {
    min-width: 240px;
}

section.page {
    flex-grow: 1;
}

footer {
    padding: 10px 0;
    text-align: center;
    border-top: 10px solid #222;
    background-color: rgb(211, 202, 118);
}
header {
    position: relative;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
nav.main {
    padding: 0 30px 30px 0;
}

ul {
    list-style-type: none;
}

nav.main a {
    display: block;
    padding: 10px;
    text-transform: uppercase;
    font-weight: bold;
    color: #222;
    text-decoration: none;
    transition: .4s;
}

nav.main a:hover {
    -webkit-transform: translate(20px);
            transform: translate(20px);
    color: #000;
}

nav.main a.active {
    color: rgb(211, 202, 118);
    border-left: 10px solid rgb(211, 202, 118);
    -webkit-transform: translate(0);
            transform: translate(0);
}
.contact h3 {
    padding-bottom: 10px;
}

.contact textarea {
    font-size: 16px;
    width: 50%;
    min-height: 200px;
    border: 2px solid #222;
    padding: 5px;
}

.contact button {
    display: block;
    margin: 0 50% 0 auto;
    border: 2px solid #222;
    background-color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    letter-spacing: 1px;
    transition: .4s;
}

.contact button:hover {
    background-color: #222;
    color: #fff;
}
.books {
    border-collapse: collapse;
    width: 100%;
    color: #222;
    font-size: 20px;
    margin-bottom: 20px;
}

.books td, .books th {
    border: 1px solid #ddd;
    padding: 8px;
}

.books thead {
    background-color: rgb(211, 202, 118);
    text-align: left;
}

.books tbody tr:nth-child(even) {
    background-color: rgb(219, 215, 179);
}

.books tbody tr:nth-child(odd) {
    background-color: #eaf1bd;
}

.books tbody td a {
    text-decoration: none;
    color: #222;
}

.books tbody td a:hover {
    text-decoration: none;
    color: rgb(176, 179, 24);
}
.login label {
    display: block;
}

.login input {
    float: center;
    margin-left: 10px;
    border: 2px solid #222;
    padding: 2px;
}

.login input[type="password"] {
    margin-left: 6px;
}

.login button {
    background-color: #fff;
    padding: 10px 20px;
    font-size: 12px;
    margin: 20px 0 0 200px;
    border: 2px solid #222;
    letter-spacing: 1px;
    cursor: pointer;
    transition: .4s;
}

.login button:hover {
    background-color: #222;
    color: white;
}

.bookPage {
    width: 100%;
    height: 100%;
    background-color: rgb(229, 235, 195);
    padding: 20px;
}

.bookPage a {
    padding: 10px;
    text-decoration: none;
    color: #222;
}

.bookPage a:hover {
    color: #777;
}

.bookContent {
    display: flex;
    padding: 20px 0;
}

.bookImg {
    width: 50%;
}

.bookImg div {
    margin: 20px auto 0;
    height: 300px;
    width: 50%;
    background-color: rgba(39, 5, 5, 0.918);
    color: white;
    text-align: center;
    line-height: 300px;
}

.bookDesc {
    width: 50%;
}
.page h2 {
    padding: 5px 0;
}

.page article {
    padding: 20px 0;
    border-bottom: 1px solid #222;
}

.page article:last-of-type {
    border: none;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
    color: #222;
}
